// src/app.ts
import { $$jump, CompileLoader, MERAK_DATA_ID, MERAK_KEEP_ALIVE, Merak, getInstance } from "merak-core";
import { defineComponent, h, onMounted, onUnmounted, watch } from "vue";

// src/share.ts
var shareEmits = {
  beforeMount: () => true,
  afterMount: () => true,
  beforeUnmount: () => true,
  afterUnmount: () => true,
  load: (params) => true,
  // script
  transformScript: (params) => true,
  // document
  tranformDocument: (params) => true
};
var shareProps = {
  name: {
    type: String,
    required: true
  },
  url: {
    type: String,
    required: true
  },
  props: {
    type: Object
  },
  proxy: {
    type: Object
  },
  keepAlive: {
    type: Boolean,
    default: false
  },
  iframe: {
    type: String
  }
};

// src/app.ts
var vueLoader = new CompileLoader();
var MerakApp = defineComponent({
  inheritAttrs: false,
  props: {
    ...shareProps,
    loaderOptions: {
      type: Object
    },
    loader: {
      type: Object,
      default: vueLoader
    },
    route: {
      type: String
    }
  },
  emits: shareEmits,
  setup(props, { emit, expose }) {
    const { url, proxy, loader, loaderOptions, props: MerakProps, iframe, name, route } = props;
    const app = getInstance(name) || new Merak(name, url, { loader, loaderOptions, proxy, iframe });
    if (MerakProps)
      app.props = MerakProps;
    for (const ev in shareEmits) {
      const task = app.lifeCycle[ev];
      const hook = (arg) => {
        task?.(arg);
        emit(ev, arg);
      };
      onMounted(() => {
        app.lifeCycle[ev] = hook;
      });
      onUnmounted(() => {
        if (app.lifeCycle[ev] === hook)
          app.lifeCycle[ev] = task;
      });
    }
    expose({ app });
    if (route)
      $$jump(props.name, route, false);
    watch(() => props.route, (n) => {
      n && $$jump(props.name, n);
    });
    return () => h("merak-app", { [MERAK_DATA_ID]: props.name, [MERAK_KEEP_ALIVE]: props.keepAlive });
  }
});

// src/block.ts
import { defineComponent as defineComponent2, h as h2, nextTick, onBeforeUnmount, onMounted as onMounted2, render } from "vue";
import { MERAK_DATA_ID as MERAK_DATA_ID2, MERAK_KEEP_ALIVE as MERAK_KEEP_ALIVE2, Merak as Merak2, createLibProxy, getInstance as getInstance2 } from "merak-core";
import { $location } from "merak-helper";
var shareNativeVars = ["document", "window", "self", "globalThis", "setTimeout", "setInterval"];
var shareLibProps = {
  nativeVars: {
    type: Array
  },
  customVars: {
    type: Array
  }
};
var MerakImport = defineComponent2({
  inheritAttrs: false,
  props: {
    ...shareLibProps,
    ...shareProps,
    fakeGlobalVar: {
      type: String,
      required: true
    },
    url: {
      type: String,
      required: false,
      default: $location().origin
    },
    head: {
      type: String
    },
    source: {
      type: String,
      required: true
    },
    deactive: {
      type: Boolean,
      default: true
    },
    globals: {
      type: Array
    }
  },
  setup(props, { slots }) {
    const { fakeGlobalVar, deactive, head, name, url, props: MerakProps, proxy, iframe, nativeVars = shareNativeVars, customVars = [], source } = props;
    const app = getInstance2(name) || new Merak2(name, url, { proxy: proxy || createLibProxy(name, url), iframe });
    if (!app.fakeGlobalVar)
      app.setGlobalVars(fakeGlobalVar, nativeVars, customVars);
    let vnode;
    onMounted2(async () => {
      await nextTick();
      if (head)
        app.sandDocument.querySelector("head").innerHTML = head;
      const { default: Comp } = await import(
        /* @vite-ignore */
        source
      );
      vnode = h2(Comp, { ...MerakProps }, slots);
      render(vnode, app.sandDocument.querySelector("body"));
    });
    onBeforeUnmount(() => {
      render(null, app.sandDocument.querySelector("body"));
      deactive && app.deactive();
    });
    return () => h2("merak-app", { [MERAK_DATA_ID2]: props.name, [MERAK_KEEP_ALIVE2]: props.keepAlive });
  }
});
var MerakScope = defineComponent2({
  inheritAttrs: false,
  props: {
    ...shareProps,
    ...shareLibProps,
    url: {
      type: String,
      required: false,
      default: $location().origin
    },
    head: {
      type: String
    },
    deactive: {
      type: Boolean,
      default: true
    },
    fakeGlobalVar: {
      type: String,
      required: true
    }
  },
  // emits: shareEmits,
  setup(props, { slots }) {
    const { fakeGlobalVar, deactive, head, name, proxy, iframe, url, nativeVars = shareNativeVars, customVars = [] } = props;
    const app = getInstance2(name) || new Merak2(name, url, { proxy: proxy || createLibProxy(name, url), iframe });
    if (!app.fakeGlobalVar)
      app.setGlobalVars(fakeGlobalVar, nativeVars, customVars);
    onMounted2(async () => {
      await nextTick();
      if (head)
        app.sandDocument.querySelector("head").innerHTML = head;
      render(slots.default()[0], app.sandDocument.querySelector("body"));
    });
    onBeforeUnmount(() => {
      render(null, app.sandDocument.querySelector("body"));
      deactive && app.deactive();
    });
    return () => h2("merak-app", { [MERAK_DATA_ID2]: props.name, [MERAK_KEEP_ALIVE2]: props.keepAlive });
  }
});

// src/ssr.ts
import { $$jump as $$jump2, MERAK_DATA_ID as MERAK_DATA_ID3, MERAK_KEEP_ALIVE as MERAK_KEEP_ALIVE3, Merak as Merak3, SSRLoader, getInstance as getInstance3 } from "merak-core";
import { defineComponent as defineComponent3, h as h3, onMounted as onMounted3, onUnmounted as onUnmounted2, watch as watch2 } from "vue";
var vueSSRLoader = new SSRLoader();
var MerakSSR = defineComponent3({
  inheritAttrs: false,
  props: {
    ...shareProps,
    loader: {
      type: Object,
      default: vueSSRLoader
    },
    route: {
      type: String
    }
  },
  emits: shareEmits,
  setup(props, { expose, emit }) {
    const { proxy, iframe, props: MerakProps, name, loader, url, route } = props;
    const app = getInstance3(name) || new Merak3(name, url, { proxy, iframe, loader });
    if (MerakProps)
      app.props = MerakProps;
    for (const ev in shareEmits) {
      const task = app.lifeCycle[ev];
      const hook = (arg) => {
        task?.(arg);
        emit(ev, arg);
      };
      onMounted3(() => {
        app.lifeCycle[ev] = hook;
      });
      onUnmounted2(() => {
        if (app.lifeCycle[ev] === hook)
          app.lifeCycle[ev] = task;
      });
    }
    if (route)
      $$jump2(props.name, route, false);
    watch2(() => props.route, (n) => {
      n && $$jump2(props.name, n);
    });
    expose({ app });
    return () => h3("merak-app", { [MERAK_DATA_ID3]: props.name, [MERAK_KEEP_ALIVE3]: props.keepAlive });
  }
});

// src/index.ts
export * from "merak-core";
export {
  MerakApp,
  MerakImport,
  MerakSSR,
  MerakScope,
  vueLoader,
  vueSSRLoader
};
