<template>
  <a-form :rules="rules" :label-col="labelCol" :wrapper-col="wrapperCol">
    <a-form-item label="" v-bind="validateInfos.info">
      <a-textarea
        placeholder=""
        :rows="4"
        v-model:value="formData.info"
      />
    </a-form-item>
    <a-form-item label="" v-bind="validateInfos.type">
      <a-select v-model:value="formData.type">
        <a-select-option
          v-for="type in types"
          :key="type.value"
          :value="type.value"
          >{{ type.label }}</a-select-option
        >
      </a-select>
    </a-form-item>
    <a-row type="flex" justify="center">
      <a-col>
        <a-button type="primary" @click="sendMessage"
          >()</a-button
        >
      </a-col>
    </a-row>
  </a-form>
</template>

<script lang="ts" setup>
import { reactive, toRaw } from 'vue'
import { Form } from 'ant-design-vue'
import { dispatchReceiveMessageEvent } from '@/utils/messageListener'
import type { Message } from '@/utils/messageListener'
import { Types } from '@/utils/messageListener'

const formData = reactive<Omit<Message, 'from'>>({
  type: Types.success,
  info: ''
})
const rules = reactive({
  info: [{ required: true, message: '!' }],
  type: [{ required: true, message: '!' }]
})
const types = reactive([
  {
    value: Types.success,
    label: ''
  },
  {
    value: Types.error,
    label: ''
  },
  {
    value: Types.warn,
    label: ''
  },
  {
    value: Types.info,
    label: ''
  }
])

const labelCol = {
  xs: { span: 24 },
  sm: { span: 3, offset: 6 }
}
const wrapperCol = {
  xs: { span: 24 },
  sm: { span: 9 }
}

const { validate, validateInfos } = Form.useForm(formData, rules)

function sendMessage() {
  validate().then(() => {
    dispatchReceiveMessageEvent(toRaw(formData))
  })
}
</script>

<style scoped lang="less"></style>
